import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {EngineerListComponent} from "./engineer-list/engineer-list.component";
import {ChecklistListComponent} from "./checklist-list/checklist-list.component";
import {ChecklistComponent} from "./check-list/check-list.component";
import {CheckFormComponent} from "./check-form/check-form.component";
import {DeleteCheckComponent} from "./delete-check/delete-check.component";

const routes: Routes = [
  { path: 'engineers', component: EngineerListComponent },
  { path: 'checklists', component: ChecklistListComponent },
  { path: 'checks/checkListId/:id', component: ChecklistComponent},
  { path: 'addCheck', component: CheckFormComponent },
  { path: 'deleteCheck/:id', component: DeleteCheckComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



