import {Component, Input, OnInit} from '@angular/core';
import {Checklist} from "../model/checklist";
import {CheckListService} from "../service/checklist-service.service";
import {CheckService} from "../service/check-service.service";
import {Check} from "../model/check";
import { ActivatedRoute, Router } from '@angular/router';
import {FormControl} from "@angular/forms";
import {Checkform} from "../form/checkform";
import {Engineer} from "../model/engineer";
import {EngineerListComponent} from "../engineer-list/engineer-list.component";
import {EngineerService} from "../service/engineer-service.service";

@Component({
  selector: 'app-checklist-list',
  templateUrl: './checklist-list.component.html',
  styleUrls: ['./checklist-list.component.css']
})
export class ChecklistListComponent implements OnInit {

  //@Input() receivedParentMessage : Engineer;

  private checklists: Checklist[];
  private checklistId: Checklist;

  // private checksCashed: Check[];
  private checksCashed: number[];
  private checkform: Checkform = new Checkform();
  engineers: Engineer[];
  engineer: Engineer;

  selectedStartDate: string;
  selectedEndDate: string;
  selectedDeadline: string;

  constructor(private engineerService: EngineerService,
              private checkListService: CheckListService,
              private checkService: CheckService,
              private route: ActivatedRoute,
              private router: Router,) {

    this.engineerService.findAllEngineers().subscribe(data => {
      this.engineers = data;
    });

    this.checkListService.findAllChecks().subscribe(data => {
      this.checklists = data;
    });
    // this.checkService.findAllChecks().subscribe(data => {
    //   this.checksCashed = data;
    // });
  }

  filterChecks() {
    console.log("checklistId =" + this.checklistId);
    this.checkService.getChecksById(this.checkform.idCheckList).subscribe(data => {
           this.checksCashed = data;
         })
  }


  selectedSDate() {
    console.log(this.selectedStartDate)
  }

  selectedEDate() {
    console.log(this.selectedEndDate)
  }

  selectedDLDate() {
    console.log(this.selectedDeadline)
  }

  checkFormBuilder() {
  }


  onSubmit() {
    this.checkService.save(this.checkform)
      .subscribe( data => {
        alert("Check created successfully.");
      });
  }
  selectedCheck(){
    console.log(JSON.stringify(this.checksCashed[0]));
    console.log(JSON.stringify(this.checkform))
  }
  ngOnInit() {
  }
}



