import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import {EngineerListComponent} from "./engineer-list/engineer-list.component";
import {ChecklistListComponent} from "./checklist-list/checklist-list.component";
// import {EngineerAndCheckComponent} from "./engineerAndCheck/engineerAndCheck.component";
import { ChecklistComponent } from './check-list/check-list.component';

import { EngineerService } from "./service/engineer-service.service";
import { CheckService } from "./service/check-service.service";
import {CheckListService} from "./service/checklist-service.service";

import {BsDatepickerModule} from 'ngx-bootstrap/datepicker/';
import { DlDateTimeDateModule, DlDateTimePickerModule } from 'angular-bootstrap-datetimepicker';
import { CheckFormComponent } from './check-form/check-form.component';
import { DeleteCheckComponent } from './delete-check/delete-check.component';

@NgModule({
  exports: [

  ],
  declarations: [
    AppComponent,
    EngineerListComponent,
    ChecklistListComponent,
    //EngineerAndCheckComponent,
    ChecklistComponent,
    CheckFormComponent,
    DeleteCheckComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    FormsModule,
    BsDatepickerModule.forRoot(),
    DlDateTimeDateModule,  // <--- Determines the data type of the model
    DlDateTimePickerModule,
    ReactiveFormsModule,
  ],
  providers: [EngineerService, CheckListService, CheckService],
  bootstrap: [AppComponent]
})
export class AppModule { }



