import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Check } from '../model/check';
import { Observable } from 'rxjs/Observable';
import {Checkform} from "../form/checkform";

@Injectable()
export class CheckService {
  private checkUrl: string;
  private checkUrlwithId: string;
  private deleteUrl : string

  constructor(private http: HttpClient) {
    this.checkUrl = 'http://localhost:8080/checks';
    this.deleteUrl = 'http://localhost:8080/deleteCheck';
  }

  public findAllChecks(): Observable<Check[]> {
    return this.http.get<Check[]>(this.checkUrl);
  }

  getChecksById(id): Observable<number[]> {
    return this.http
      .get<number[]>(this.checkUrl + '/checkListId/' + id);
  }

  getCheckById(id): Observable<Check> {
    return this.http
      .get<Check>(this.checkUrl + '/' + id);
  }

  public save(check: Checkform) {
    return this.http.post<Check>(this.checkUrl, check);
  }

  public delete(check: Check) {
    return this.http.delete<Check>(this.deleteUrl + '/' + check.idCheck);
  }

  // public delete(check: Check) {
  //   return this.http.post<Check>(this.deleteUrl, check);
  // }

}



