import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Checklist } from '../model/checklist';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class CheckListService {
  private checkUrl: string;
  private checkUrlwithId: string;

  constructor(private http: HttpClient) {
    this.checkUrl = 'http://localhost:8080/checklists';
 //   this.checkUrlwithId = 'http://localhost:8080/checklists/'+ Checklist.prototype.id;
  }

  public findAllChecks(): Observable<Checklist[]> {
    return this.http.get<Checklist[]>(this.checkUrl);
  }

  // public findAllChecksById(): Observable<Checklist[]> {
  //   return this.http.get<Checklist[]>(this.checkUrlwithId);
  // }

  /*public save(user: User) {
    return this.http.post<User>(this.usersUrl, user);
  }*/
}



