import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {CheckService} from "../service/check-service.service";
import {Check} from "../model/check";

@Component({
  selector: 'app-delete-check',
  templateUrl: './delete-check.component.html',
  styleUrls: ['./delete-check.component.css']
})
export class DeleteCheckComponent implements OnInit {

  private choosenCheck : Check;
  private checkNumber : number;

  constructor(private router : Router,
              private route: ActivatedRoute,
              private checkService: CheckService) { }

  ngOnInit() {
  }

  onSubmit() {
    this.checkService.delete(this.choosenCheck)
      .subscribe( data => {
        alert("Check deleted successfully.");
      });
  }

  // onSubmit() {
  //   this.checkService.delete(this.choosenCheck)
  //     .subscribe( data => {
  //       alert("Check deleted successfully.");
  //     });
  // }

  showId() {
    this.checkService.getCheckById(this.checkNumber).subscribe(data => {
      this.choosenCheck = data;
    })
    console.log(this.checkNumber)
    console.log(JSON.stringify(this.choosenCheck))
  }

}



