import { Component, OnInit } from '@angular/core';
import {Engineer} from "../model/engineer";
import {EngineerService} from "../service/engineer-service.service";
import { NgForm } from '@angular/forms';



@Component({
  selector: 'app-engineer-list',
  templateUrl: './engineer-list.component.html',
  styleUrls: ['./engineer-list.component.css']
})
export class EngineerListComponent implements OnInit {

  engineers: Engineer[];
  engineer: Engineer;

  constructor(private engineerService: EngineerService) {
  }

  ngOnInit() {
    this.engineerService.findAllEngineers().subscribe(data => {
      this.engineers = data;
    });
  }
  //
  // templateForm(value: any) {
  //   alert(JSON.stringify(value));
  // }
  //
  showEngineerForm() {
    alert(JSON.stringify(this.engineer))
  }

  public getEngineer() {
    return this.engineer;
  }

}



