import { Component, OnInit } from '@angular/core';
import {CheckService} from "../service/check-service.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Check} from "../model/check";
import {Checkform} from "../form/checkform";

@Component({
  selector: 'app-check-form',
  templateUrl: './check-form.component.html',
  styleUrls: ['./check-form.component.css']
})
export class CheckFormComponent implements OnInit {

  check : Check;
  //newcheck: Check;
  private checksCashed: Check[];

  private checkform: Checkform;

  constructor(private checkService: CheckService,
              private route: ActivatedRoute,
              private router: Router,
  )
  {

    this.checkService.findAllChecks().subscribe(data => {
      this.checksCashed = data;
    });
  }

  ngOnInit() {
  }

  checkFormBuilder() {
    this.checkform = new Checkform();
    this.checkform.idCheckList = this.check.idCheckList;
    //this.checkform.name = this.check.name;
    this.checkform.start = this.check.start;
    this.checkform.finish = this.check.finish;
    this.checkform.deadline = this.check.deadline;
    this.checkform.idEngineer = this.check.idEngineer;
    //this.checkform.responible = this.check.responible;

  }


  onSubmit() {
    this.checkFormBuilder();
    //console.log(this.check.idCheck)

    console.log(JSON.stringify(this.checkform));
    this.checkService.save(this.checkform)
      .subscribe( data => {
        alert("Check created successfully.");
      });
  }

  selectedCheck(){
    console.log(this.check)
  }


}



