import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {Check} from "../model/check";
import {CheckService} from "../service/check-service.service";
import {CheckListService} from "../service/checklist-service.service";
import {ChecklistListComponent} from "../checklist-list/checklist-list.component";

@Component({
  selector: 'app-check-list',
  templateUrl: './check-list.component.html',
  styleUrls: ['./check-list.component.css']
})
export class ChecklistComponent implements OnInit {

  @Input() checkListId: number;

  checks: number[];
  check: Check;

  constructor(private checkService: CheckService) {
  }

  ngOnInit() {
    if (this.checkListId > 0) {
      this.getChecks();
    }
  }

  getChecks() {
    this.checkService.getChecksById(this.checkListId).subscribe(data => {
      this.checks = data;
    });
  }
}



